}

.site-footer p {
    font-size: 24px;
    font-size: 1.5rem;
    line-height: 1em;
    margin: 0;
    color: #eae8e8;
    position: relative;
    z-index: 2;
}

@media (max-width: 767px) {
    .site-footer p {
        font-size: 20px;
        font-size: 1.25rem;
    }
}

ul.smooth-scroll {
    position: fixed;
    bottom: 0px;
    right: 30px;
    list-style: none;
}
ul.smooth-scroll a {
    width: 50px;
    height: 50px;
    background: #4da8da;
    color: #fff;
    display: block;
    border-radius: 50%;
    text-align: center;
    line-height: 50px;
}

h1,
h2,
h3 {
  font-family: "Great Vibes", cursive;
}
body {
  font-family: "Muli", sans-serif;
}

a {
  text-decoration: none;
  transition: all 0.3s;
}
a:hover {
  text-decoration: none !important;
}
img {
  width: 100%;
}
.section-padding {
  padding: 180px 0 130px;
}

.App {
  text-align: left;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 767px) {
  .section-padding {
    padding: 130px 0 100px;
  }
}

